/*
.sonic { 
	color:rgba(78.62500101327896, 12.121354322880507, 12.121354322880507, 1);
	width:217px;
	height:35px;
	position:absolute;
	left:60px;
	top:429px;
	font-family:Inter;
	text-align:center;
	font-size:32px;
	letter-spacing:0;
}
*/
.sonic {
	padding: 12px 24px;
	border: 1px solid #4f4f4f;
	border-radius: 4px;
	transition: all 0.2s ease-in;
	width:217px;
	height:35px;
	position:absolute;
	text-align: center;
	left:60px;
	top:530px;
	align-items: center;
	font-family:Inter;
	overflow: hidden;
	font-size: 19px;
	color: black;
	z-index: 1;
} 
.sonic:before {
	content: "";
	position: absolute;
	left: 50%;
	transform: translateX(-50%) scaleY(1) scaleX(1.25);
	top: 100%;
	width: 140%;
	height: 180%;
	background-color: rgba(0, 0, 0, 0.05);
	border-radius: 50%;
	display: block;
	transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
	z-index: -1;
}
.sonic:after {
	content: "";
	position: absolute;
	left: 55%;
	transform: translateX(-50%) scaleY(1) scaleX(1.45);
	top: 180%;
	width: 160%;
	height: 190%;
	background-color: #39bda7;
	border-radius: 50%;
	display: block;
	transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
	z-index: -1;
}  
.sonic:hover {
	color: #ffffff;
	border: 1px solid #39bda7;
	box-shadow: 2px 3px rgb(95, 93, 93);
   }
.sonic:hover:before {
	top: -35%;
	background-color: #39bda7;
	transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
}
.sonic:hover:after {
	top: -45%;
	background-color: #39bda7;
	transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
}
